.text-block {
    $this: &;

    margin: 100px 0;

    @include desktop {
        margin: 150px percentage(290/1675) 150px percentage(573/1675);
    }

    &--two-columns {
        @include desktop {
            margin-right: percentage(145/1675);
            margin-left: percentage(430/1675);

            #{$this}__text {
                columns: auto 2;
                gap: 40px;
            }
        }
    }

    &__title {
        font-family: $ff-financier-text;
        font-weight: $fw-regular;
        font-size: 25px;
        line-height: 1.2;
        margin-bottom: 15px;

        @include desktop {
            font-size: 30px;
            line-height: 1.3;
            margin-bottom: 32px;
        }

        i {
            display: block;
        }
    }
}
