.cta-block {
    margin: 60px 0;

    @include desktop {
        margin: 90px percentage(140/1675);
        display: grid;
        gap: 40px;
        grid-template-columns: 387fr 676fr 245fr ;
    }

    &__title {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 30px;
        line-height: 1.16;
        margin-bottom: 18px;

        @include desktop {
            font-size: 40px;
            margin-bottom: 0;
        }
    }

    &__info {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-style: italic;
        font-size: 20px;
        line-height: 1.5;
        margin-bottom: 35px;

        @include desktop {
            font-size: 22px;
            line-height: 1.45;
            padding-top: 2px;
            margin-bottom: 0;
        }
    }

    &__btn-area {
        @include desktop {
            display: flex;
            justify-content: flex-end;
            align-items: flex-start;
            padding-top: 10px;
        }
    }
}
